export default {
  bind (el) {
    /*  */
    el.classList.add("wc-messagebox__mask---");

    /* init */
    el.classList.add("wc-messagebox__mask-transition---");
    el.classList.add("wc-messagebox__mask-diff---");

    /*  */
    setTimeout(() => {
      el.classList.remove("wc-messagebox__mask-diff---");
    }, 50);
  }
};



