import Vue from "vue";
import ConfirmContainer from "./tpl";
import iOSConfirm from "./iOSConfirm";
import merge from "lodash/merge";

let instance;
let globalConfig = {};
let ConfirmConstructor = Vue.extend(ConfirmContainer);

let initInstance = () => {
  instance = new ConfirmConstructor({
    el: document.createElement("div")
  });
  document.body.appendChild(instance.$el);
};
let Confirm = (options) => {
  initInstance();

  /*
       options , :
      this.$Confirm("content");
      

       options , :
      this.$Confirm({
          title: xxx //  title , .
      });

      ,  < . ?
       title, 

      , options ,  components .

  */

  /*  this.$Confirm(string)  */
  if (typeof options === "string") {
    options = {
      content: options
    };
  }

  //  Confirm  
  merge(instance.$data.w, globalConfig);
  //  Confirm instance 
  merge(instance.$data.w, options);

  return new Promise((resolve, reject) => {
    instance.show = true;
    /*
         components ,  instance.$data.components 
        components :
        {
            Foo: Foo
        }
         key  instance.$data  activeView,  <component :is="activeView">
        
         components  Foo
    */

    /*  components */

    let userComponent = instance.$data.w.component;

    if (userComponent) {
      for (let key in userComponent) {
        instance.$options.components[key] = userComponent[key];
      }
      /*  activeView */
      instance.activeView = Object.keys(userComponent)[0];
    } else {
      instance.$options.components["iOS"] = iOSConfirm;
      instance.activeView = "iOS";
    }

    /* fix  */
    let inputs = Array.prototype.slice.call(document.querySelectorAll("input"));
    inputs.forEach((input) => {
      input.blur();
    });

    /*  success  */
    let success = instance.success;
    let fail = instance.fail;

    instance.success = () => {
      success();
      resolve("success");
    };

    instance.fail = () => {
      fail();
      resolve("fail");
      // reject(new Error("fail"));
    };
  });
};
export default {
  install(Vue, options = {}) {
    globalConfig = options;
    Vue.prototype.$confirm = Confirm;
  }
};



