import Vue from "vue";
import AlertContainer from "./tpl";
import iOSAlert from "./iOSAlert";
import merge from "lodash/merge";

let instance;
let globalConfig = {};
let AlertConstructor = Vue.extend(AlertContainer);

let initInstance = () => {
  instance = new AlertConstructor({
    el: document.createElement("div")
  });
  document.body.appendChild(instance.$el);
};
let Alert = (options) => {
  initInstance();

  /*
     options , :
    this.$alert("content");
    

     options , :
    this.$alert({
        title: xxx //  title , .
    });

    ,  < . ?
     title, 

    , options ,  components .
  */

  /*  this.$alert(string)  */
  if (typeof options === "string") {
    options = {
      content: options
    };
  }

  //  Alert  
  merge(instance.$data.w, globalConfig);
  //  Alert instance 
  merge(instance.$data.w, options);

  return new Promise((resolve, reject) => {
    instance.show = true;
    /*
         components ,  instance.$data.components 
        components :
        {
            Foo: Foo
        }
         key  instance.$data  activeView,  <component :is="activeView">
        
         components  Foo
    */

    /*  components */
    let userComponent = instance.$data.w.component;

    if (userComponent) {
      for (let key in userComponent) {
        instance.$options.components[key] = userComponent[key];
      }
      /*  activeView */
      instance.activeView = Object.keys(userComponent)[0];
    } else {
      instance.$options.components["iOS"] = iOSAlert;
      instance.activeView = "iOS";
    }

    /* fix  */
    let inputs = Array.prototype.slice.call(document.querySelectorAll("input"));
    inputs.forEach((input) => {
      input.blur();
    });

    /*  success  */
    let success = instance.success;

    instance.success = () => {
      success();
      resolve("ok");
    };
  });
};
export default {
  install(Vue, options = {}) {
    globalConfig = options;
    Vue.prototype.$alert = Alert;
  }
};



